        currentPage: 1,//页面打开的时候显示第几页
        totle:0,//数据总条数
        tiao:0,//记录每页显示多少条
        
        hang:-1,
        suo:-1,
        a:'',
        
        
        dialogVisible: false,
        xiangbox:false,
        num:null,
        
        sousuofs:0,//0是综合查询 1是u9查询
        
        dingdanhao:'',//订单号
        kehumingcheng:'',//客户名称
        chuhuodanhao:'',//出货单号
        
        u9dingdanhao:'',//u9查询订单号
        
        info:{},//查看详情
        
        u9ma:0,//0是综合查询模板
      	
      }
  },
  components: {
    HeadNav,
    LeftMenu
  },
  methods: {
  	
  	//u9添加 接口
  	plus(row){
  		console.log(row);
  		this.$axios.post(`/api/sale/sear`,{
			arr:[
					{
						"accountConditons": row.AccountConditons,
					    "accountDate": row.AccountDate,
					    "accountPeriod": row.AccountPeriod,
					    "businesser": row.Businesser,
					    "clientCode": row.ClientCode,
					    "clientName": row.ClientName,
					    "contractPeriod": row.ContractPeriod,
					    "currency": row.Currency,
					    "documentary": row.Documentary,
					    "dueDate": row.DueDate,
					    "itemName": row.ItemName,
					    "orderCreationDate": row.OrderCreationDate,
					    "priceTotal": row.PriceTotal,
					    "quantity": row.Quantity,
					    "saleNumber": row.SaleNumber,
					    "salerCode": row.SalerCode,
					    "shipmentConfirmationDate": row.ShipmentConfirmationDate,
					    "shipmentNumber": row.ShipmentNumber,
					    "taxrate": row.Taxrate,
					    "u9Id": row.U9Id,
					    "unitPrice": row.UnitPrice,
					    "userId": row.UserId,
				    },
				],	
		}).then(res => {
		  	
            console.log(res);
            
	  	})
  	},
  	
  	//u9下拉
  	selectf(zhi){
 
  		if(zhi=='销售单号'){
  			this.select = 1;
  		}else{
  			this.select = 2;
  		}
  	
  		
  	},
  	
  	//u9查询
  	u9search(){
  		this.u9ma = 1;
  		this.$axios.post(`/api/sale/sear`,{
		  "doc": this.u9dingdanhao,
		  "type": this.select
		}).then(res => {
		  	
            this.tableDatau9 = res.data.data.data;
            
	  	})
  	},
  	
