<template>
	<div class="receipts">
	<HeadNav></HeadNav>
    <LeftMenu></LeftMenu>
    
    <div class="rightContainer">
    	    
		<div style="width: 60%; margin:30px auto 30px auto;">
			<el-input placeholder="请输入内容" v-model="input3" class="input-with-select">
				<el-select v-model="select" slot="prepend" placeholder="请选择">
					<el-option label="餐厅名" value="1"></el-option>
					<el-option label="订单号" value="2"></el-option>
					<el-option label="用户电话" value="3"></el-option>
				</el-select>
				<el-button slot="append" icon="el-icon-search"></el-button>
			</el-input>
		</div>
		
		<el-dialog
		  title="修改订单"
		  :visible.sync="dialogVisible"
		  width="60%"
		  :before-close="handleClose">
		  
		  
		  	<div class="demo-input-suffix">
			  属性方式：
			  <el-input v-model="input" placeholder="请输入内容" style='width:60%'></el-input>
			</div>
			
			
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="dialogVisible = false">取 消</el-button>
		    <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
		  </span>
		</el-dialog>

    	
    	<div class="tablebox">

				<el-table :data="tableData" style="width: 100%" height="600">
					<el-table-column prop="Id" label=" id" width="150">
					</el-table-column>
					<el-table-column prop="CreateAt" label="创造" width="120">
					</el-table-column>
					<el-table-column prop="UpdateAt" label="更新" width="120">
					</el-table-column>
					<el-table-column prop="DeleteAt" label="删除时间" width="120">
					</el-table-column>
					<el-table-column prop="excel_id" label="表格ID" width="120">
					</el-table-column>
					<el-table-column prop="time" label="进款时间" width="120">
					</el-table-column>
					<el-table-column prop="type" label="进款类别" width="120">
					</el-table-column>
					<el-table-column prop="status" label="状态" width="120">
					</el-table-column>
					<el-table-column prop="client" label="进款客户名称" width="120">
					</el-table-column>
					<el-table-column prop="remarks" label="备注" width="120">
					</el-table-column>
					<el-table-column prop="amount" label="进款" width="120">
					</el-table-column>
					<el-table-column prop="used" label="已销款" width="120">
					</el-table-column>
					<el-table-column prop="balance" label="余额" width="120">
					</el-table-column>
					<el-table-column prop="Version" label="版本" width="120">
					</el-table-column>

					<el-table-column fixed='right' label="操作" width="300">
						<template slot-scope="scope">
							
						  <el-button  size="mini" @click="dialogVisible = true">编辑</el-button>

						  <el-button size="mini" type="danger" @click="handleDelete(scope.row)">删除</el-button>
						  <el-button size="mini" type="success" @click='details(scope.row)'>详情</el-button>
						</template>
					</el-table-column>
				</el-table>
				
				<div style="margin-top: 30px;">
					<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[20, 3, 160, 170]" :page-size="20" layout="total, sizes, prev, pager, next, jumper" :total="totle">
					</el-pagination>
				</div>
				
			</div>
      <router-view></router-view>
    </div>
	</div>
</template>

<script>
import HeadNav from "../components/HeadNav";
import LeftMenu from "../components/LeftMenu";
import Qs from 'qs';
export default {
  name: "Order",
  data(){
  	return {
        tableData: [],
        
        input3: '',//这个必须要加上 搜索框的
      	select: '',//这个必须要加上 搜索框的
        currentPage: 1,//页面打开的时候显示第几页
        totle:0,//数据总条数
        tiao:0,//记录每页显示多少条
        
        dialogVisible: false,
        input:'',
        
        
        
        currentPage: 1,//页面打开的时候显示第几页
        totle:0,//数据总条数
        tiao:0,//记录每页显示多少条
      	
      }
  },
  components: {
    HeadNav,
    LeftMenu
  },
  methods: {
  	
  		handleClose(done) {
	        this.$confirm('确认关闭？')
	          .then(_ => {
	            done();
	          })
	          .catch(_ => {});
       },

  	
		/*handleEdit(index,row){//编辑

		  	this.suoindex = index;
		  	this.hang.push(row.id);
	
		  	
		  	
		},*/
		
		//详情
		details(row){
			console.log(row);
			this.$axios.post(`/api/co/info`,Qs.stringify({id:row.Id})).then(res => {
		  		
		  		console.log(res);
	
		  	})
		},
		
		save(index,row){//保存
			
			this.suoindex = -1;//编辑时候使用 等于-1的时候都能够点击
			
			this.hang = [];
			
		},
		
		youid(id){//判断是禁止还是写入状态
			
			return this.hang.includes(id);
			
		},
		
		handleDelete(row){
			console.log(row.Id);
			this.$axios.post(`/api/sale/del`,Qs.stringify({id:row.Id})).then(res => {
		  		
		  		console.log(res);
	
		  	})
			
		},
		
		handleSizeChange(val) {
		
		//console.log(`每页 ${val} 条`);
		this.tiao = val;
		this.$axios.post(`/api/sale/search?page=${val}`).then(res => {
	  		
	  		this.tableData = res.data.data.sale;

	  	})
	    
	    
		},
		handleCurrentChange(val) {
		    //console.log(`当前页: ${val}`);
		    this.$axios.post(`/api/sale/search?number=${val}&page=${this.tiao}`).then(res => {
		  		
		  		this.tableData = res.data.data.sale;
	
		  	})
		},
	},
	
  	mounted(){
  	
	  	this.$axios.post("/api/co/l").then(res => {
	  		
	  		this.tableData = res.data.data.comes;
	  		this.totle = res.data.data.total;
	  		
	  		
	  	})

	}
  
}
</script>

<style scoped>
.receipts {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.rightContainer {
  position: relative;
  top: 0;
  left: 180px;
  width: calc(100% - 180px);
  height: calc(100% - 71px);
  overflow: auto;
}

.tablebox{
	
}

.el-select .el-input {
	width: 130px;
}

.input-with-select .el-input-group__prepend {
	background-color: #fff;
}
</style>