<template>
    <div class="login">
        <section class="form_container">
            <div class="manage_tip">
                <span class="title">青岛后台管理系统</span>
            </div>
            <el-form :model="loginUser" :rules="rules" ref="loginForm" class="loginForm" label-width="60px">
                <el-form-item label="用户名">
                    <el-input  v-model="loginUser.name" placeholder="请输入用户名"></el-input>
                </el-form-item>
                <el-form-item label="密码" >
                    <el-input v-model="loginUser.password" placeholder="请输入密码" type="password"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary"  @click="submitForm('loginForm')" class="submit_btn">登  录</el-button>
                </el-form-item>
                
            </el-form>
        </section>
    </div>
</template>

<script>
import jwt_decode from "jwt-decode";
import Qs from 'qs';
export default {
  name: "login",
  data() {
    return {
      loginUser: {
        name: "",
        password: ""
      },
      rules: {
        
        
      }
    };
  },
  methods: {
    submitForm(formName) {

      this.$refs[formName].validate(valid => {
        if (valid) {//JSON.stringify(this.loginUser)
        	console.log(this.loginUser)
        	var a = {
        		"name":'aaa',
        		"password":'123456'
        	}
          this.$axios.post("/api/user/check",this.loginUser).then(res => {
            
            // 登录成功
            const { data } = res.data;
            const token = data;
            localStorage.setItem("eleToken", token);
  
            // 解析token
            const decode = jwt_decode(token);

            // 存储数据
            this.$store.dispatch("setIsAutnenticated", !this.isEmpty(decode));
            this.$store.dispatch("setUser", decode);

            // 页面跳转
            this.$router.push("/index");
           
          }).catch((error)=>{
             console.log(error)
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    isEmpty(value) {
      return (
        value === undefined ||
        value === null ||
        (typeof value === "object" && Object.keys(value).length === 0) ||
        (typeof value === "string" && value.trim().length === 0)
      );
    }
  }
};
</script>

